/*
 CSS-:
--theme-default- #  
--theme-light-   #  "light"
--theme-dark-    #  "dark"
--theme-neutral- #  "neutral"
*/

export const changeCssVars = theme => {
    const root = document.querySelector(':root');

    const cssVars = ['header', 'bgimg'];

    cssVars.forEach(el => {
        root.style.setProperty(`--theme-default-${el}`, `var(--theme-${theme}-${el})`)

    });
};
